<template>
  <div>
    <el-dialog
      title=""
      :visible="upShow"
      :modal-append-to-body="false"
      :before-close="onClose"
      width="30"
    >
      <el-upload
        class="upload-demo"
        ref="upload"
        action=""
        :http-request="myUploadFile"
        :on-remove="handleRemove"
        :on-change="handlChange"
        :on-exceed="handlExceed"
        :file-list="fileList"
        :limit="1"
        accept=".xlsx,.xls"
        :auto-upload="false"
      >
        <el-button slot="trigger" type="success"></el-button>
        <div class="text">xls/xlsx</div>
      </el-upload>
    </el-dialog>
  </div>
</template>
<script>
let upLoad = "";
export default {
  props: {
    upShow: {
      type: Boolean,
      require: true,
    },
    type: {
      type: Number,
      require: true,
    },
  },
  data() {
    return {
      fileList: [],
    };
  },
  created() {
    console.log(this.type);
  },
  methods: {
    /**
     * 
     */
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },

    /**
     * 
     */
    handlChange(file, fileList) {
      this.fileList = fileList;
    },
    /**
     * 
     * @param data
     */
    myUploadFile(data) {
      let file = data.file;
      let formData = new FormData();
      formData.append("file", file);
      const loading = this.$loading({
        lock: true,
        text: "",
      });
      this.$axios
        .post(this.$api + upLoad, formData)
        .then((res) => {
          console.log(res);
          loading.close();
          this.submitLoading = false;
          this.$confirm("", "", {
            confirmButtonText: "",
            type: "success",
            showCancelButton: false,
            closeOnClickModal: false,
            center: true,
          });
        })
        .catch(() => {
          loading.close();
          this.submitLoading = false;
        });
    },

    /**
     * 
     */
    submitUpload() {
      this.submitLoading = true;
      if (this.fileList.length <= 0) {
        this.$message.error("");
        this.submitLoading = false;
        return;
      }
      this.$refs.upload.submit(); //:http-request="",,http-request
    },
    /**
     * 
     */
    handlExceed() {
      this.$message.error("");
    },
    onClose() {
      this.$emit("update:upShow", false);
    },
  },
};
</script>
