// import Vue from 'vue'
let socket = null;
let lockReconnet = false; // 
const wsUrl = "ws://192.168.20.162:24912";
let isReconnet = false;
let globalCallback = null;
let sendData = null; // socketsocket
let createSocket = (url) => {
  // socket
  try {
    if ("WebSocket" in window) {
      socket = new WebSocket(url);
    } else if ("MozWebSocket" in window) {
      // eslint-disable-next-line no-undef
      socket = new MozWebSocket(url);
    }
    // Vue.prototype.socket = socket //this.socket.close()
    initSocket();
  } catch (e) {
    reconnet(url);
  }
};
let sendMsg = (data, callback) => {
  // ,
  if (socket.readyState === 1) {
    globalCallback = callback;
    sendData = data;

    data = JSON.stringify(data);
    socket.send(data);
  } else {
    setTimeout(() => {
      console.log(socket, "socket");
      sendMsg(data, callback);
    }, 1500);
    return false;
  }
  socket.onmessage = (ev) => {
    callback && callback(ev);
  };
};
let initSocket = () => {
  // websocket
  socket.onopen = () => {
    console.log("socket");
    // heartCheck.reset().start() // 

    if (isReconnet) {
      // 
      // console.log('websocket')
      sendMsg(sendData, globalCallback);
      isReconnet = false;
    }
  };

  socket.onmessage = (ev) => {
    console.log(ev, "11111111111111");
    heartCheck.reset().start(); // 
  };

  socket.onerror = () => {
    console.log("websocket---onerror");
    reconnet(wsUrl);
  };

  socket.onclose = () => {
    console.log("websocket+++onclose");
    reconnet(wsUrl);
  };
};
let reconnet = (url) => {
  // websock
  if (lockReconnet) return false;

  isReconnet = true;
  lockReconnet = true;
  setTimeout(() => {
    createSocket(url);
    lockReconnet = false;
  }, 2000);
};
let heartCheck = {
  // 
  timeout: 60 * 1000,
  timeoutObj: null,
  serverTimeoutObj: null,
  reset() {
    clearTimeout(this.timeoutObj);
    clearTimeout(this.serverTimeoutObj);
    return this;
  },
  start() {
    let that = this;
    this.timeoutObj = setTimeout(() => {
      // onmessage,onmessagereset
      socket.send("heart check");
      this.serverTimeoutObj = setTimeout(() => {
        socket.close();
      }, that.timeout);
    }, this.timeout);
  },
};
createSocket(wsUrl);
export default { sendMsg };
