import * as echarts from "echarts";
//
const dataAssembly = () => {
  const assArray = [];
  //
  const typesStatus = [
    {
      name: "",
      color: "#2f4554",
    },
    {
      name: "",
      color: "#d48265",
    },
    {
      name: "",
      color: "#c23531",
    },
  ];
  //
  const categories = ["1", "2", "3", "4"];
  //
  const nowData = new Date().getTime();
  //
  const inThePastTime = nowData - 1000 * 60 * 60 * 2;
  //
  //
  const timerArr = [];
  //
  const stateArr = [];
  const baseAssData = (timer) => {
    for (let index = 0; index < 24; index++) {
      stateArr.push(typesStatus[Math.floor(Math.random() * 3)]["name"]);
      timerArr.push(timer + 1000 * 60 * 5 * index);
    }
  };
  // console.log(stateArr);
  // console.log(timerArr);
  baseAssData(inThePastTime);
  //
  const assvalue = () => {
    for (let idx = 0; idx < 4; idx++) {
      for (let i = 0; i < 24; i++) {
        assArray.push({
          //
          name: categories[idx],
          value: [
            // y
            idx,
            //
            timerArr[i],
            // i===23 ,timerArr[i+1] 
            i === 23 ? timerArr[23] + 1000 * 60 * 5 : timerArr[i + 1],
          ],
          //
          itemStyle: {
            normal: {
              color: typesStatus[Math.floor(Math.random() * 3)]["color"],
            },
          },
        });
      }
    }
    return assArray;
  };
  return assvalue();
  // console.log(assArray);
};
// 
const formattered = (val) => {
  if (parseInt(val) < 10) {
    val = "0" + val;
  }
  return val;
};
const formatters = (value) => {
  const date = new Date(value);
  return (
    formattered(date.getHours()) +
    ":" +
    formattered(date.getMinutes()) +
    ":" +
    formattered(date.getSeconds())
  );
};
//
const formateText = (params) => {
  switch (params) {
    case "#95EC69":
      return "";
    case "#FFC600":
      return "";
    case "#3A80EA":
      return "";
    case "#1583A2":
      return "";
    case "#00B8F5":
      return "";
    case "#EF4848":
      return "";
    case "#C1CDC1":
      return "";
    default:
      break;
  }
};
//  //echarts
const renderItem = (params, api) => {
  // renderItem 
  const categoryIndex = api.value(0); // api.value(0)  dataItem 
  const start = api.coord([api.value(1), categoryIndex]); //  api.coord(...) 
  const end = api.coord([api.value(2), categoryIndex]);
  const height = api.size([0, 1])[1] * 0.6;
  return {
    type: "rect",
    shape: echarts.graphic.clipRectByRect(
      {
        // 
        x: start[0],
        y: start[1] - height / 2,
        width: end[0] - start[0],
        height: height,
      },
      {
        // 
        x: params.coordSys.x,
        y: params.coordSys.y,
        width: params.coordSys.width,
        height: params.coordSys.height,
      }
    ),
    style: api.style(),
  };
};
export { dataAssembly, formattered, formatters, formateText, renderItem };
